/* eslint-disable complexity */
import React from 'react';
import { describe } from 'react-desc';
import Search from '@elliemae/ds-icons/Search';
import { DSIconColors } from '@elliemae/ds-icon';
import CloseXsmall from '@elliemae/ds-icons/CloseXsmall';
import { SearchBoxTextBox } from './styled';
import { SButton } from './SButton';
import { useSearch } from './useSearch';
import { searchBoxProps } from './props';
import { defaultProps } from './defaultProps';

const DSSearchBox = ({
  className,
  clearable,
  containerProps,
  disableButton,
  disabled,
  innerRef,
  onBlur,
  onChange,
  onKeyUp,
  onSearch,
  placeholder,
  property,
  readOnly,
  searchOnEnter,
  onSearchButtonClick,
  showIcon,
  style,
  value,
}) => {
  const {
    searchValue,
    handleChange,
    handleSearch,
    handleKeyUp,
    handleClear,
  } = useSearch({
    onChange,
    onKeyUp,
    onSearch,
    onSearchButtonClick,
    property,
    searchOnEnter,
    value,
  });
  const shouldShowClearIcon = showIcon && clearable && searchValue?.length > 0;
  const shouldShowSearchIcon = !shouldShowClearIcon && showIcon;
  const shouldShowAnyIcon = shouldShowClearIcon || shouldShowSearchIcon;

  const PureSearchboxIcon = React.useMemo(() => {
    let Icon = <Search />;
    let handleOnClick = () => handleSearch(true);
    if (!shouldShowAnyIcon) return null;
    if (shouldShowClearIcon) {
      Icon = <CloseXsmall color={DSIconColors.PRIMARY} />;
      handleOnClick = handleClear;
    }

    return (
      <SButton
        disabled={disabled || disableButton}
        icon={Icon}
        onClick={handleOnClick}
      />
    );
  }, [
    shouldShowClearIcon,
    shouldShowSearchIcon,
    shouldShowAnyIcon,
    disabled,
    disableButton,
    handleSearch,
    handleClear,
  ]);

  return (
    <div style={{ position: 'relative', zIndex: 0 }}>
      <SearchBoxTextBox
        className={className}
        containerProps={containerProps}
        disabled={disabled}
        innerRef={innerRef}
        onChange={handleChange}
        onKeyUp={handleKeyUp}
        onBlur={onBlur}
        placeholder={placeholder}
        readOnly={readOnly}
        style={style}
        value={searchValue}
      />
      {PureSearchboxIcon}
    </div>
  );
};

DSSearchBox.propTypes = searchBoxProps;
DSSearchBox.defaultProps = defaultProps;

const SearchBoxWithSchema = describe(DSSearchBox);
SearchBoxWithSchema.propTypes = searchBoxProps;

export { SearchBoxWithSchema };
export default DSSearchBox;
