import { useState, useEffect, useCallback } from 'react';
import { debounce } from '@elliemae/ds-utilities/utils';

// eslint-disable-next-line max-params
function handleSearch(
  clearOnSearch,
  setValue,
  searchOnEnter,
  value,
  onSearch,
  property,
  clickTrigger,
  onSearchButtonClick,
) {
  if (clickTrigger) {
    onSearchButtonClick();
  }
  if (clearOnSearch) setValue('');
  if (searchOnEnter && !value) return;
  onSearch({
    query: { [property]: value },
    property,
    value,
  });
}

export function useSearch(params = {}) {
  const {
    onKeyUp,
    onSearch,
    onSearchButtonClick,
    property,
    clearOnSearch,
    searchOnEnter,
    onChange,
  } = params;
  const [value, setValue] = useState(params.value);
  const debouncedSearch = useCallback(debounce(handleSearch, 200), []);
  // update state if prop changes
  useEffect(() => {
    if (value !== params.value) {
      setValue(params.value);
    }
  }, [params.value]);

  const searchWithParams = (clickedSearchButton = false) =>
    handleSearch(
      clearOnSearch,
      setValue,
      searchOnEnter,
      value,
      onSearch,
      property,
      clickedSearchButton,
      onSearchButtonClick,
    );

  const handleChange = ({ target }) => {
    onChange(target.value);
    setValue(target.value);
    if (!searchOnEnter) {
      // Functional update to get latest value state, similar to this.setState callback
      setValue((prevValue) => {
        debouncedSearch(
          clearOnSearch,
          setValue,
          searchOnEnter,
          prevValue,
          onSearch,
          property,
        );
        return prevValue;
      });
    }
  };

  const handleClear = () => {
    onChange('');
    setValue('');
    if (!searchOnEnter) {
      // Functional update to get latest value state, similar to this.setState callback
      setValue((prevValue) => {
        debouncedSearch(
          clearOnSearch,
          setValue,
          searchOnEnter,
          prevValue,
          onSearch,
          property,
        );
        return prevValue;
      });
    }
    debouncedSearch(
      clearOnSearch,
      setValue,
      searchOnEnter,
      value,
      onSearch,
      property,
    );
  };

  const handleKeyUp = (e) => {
    const { key } = e;
    if (searchOnEnter && key === 'Enter') {
      searchWithParams();
    }
    onKeyUp(e);
  };

  return {
    searchValue: value,
    handleChange,
    handleSearch: searchWithParams,
    handleKeyUp,
    handleClear,
  };
}
